.container{
   
   position: relative;
   bottom: 0px;
   width: 100%;
   height: 15%;
   background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,13,121,1) 0%, rgba(1,186,238,1) 93%, rgba(0,212,255,1) 100%);
}


.iconBrands{
   display: flex;
   justify-content: space-evenly;
   /*invert function inverts the image from black to aliceblue
   drop-shadow function to create a shadow effect on the icons*/
   filter: drop-shadow(0px 5px 5px aliceblue) invert(100%);
   padding-top: 5px;
}

.yearEstablished{
   display: flex;
   justify-content: center;
   /*invert function inverts the content from black to aliceblue
   drop-shadow function to create a shadow effect on the label and icon*/
   filter: drop-shadow(0px 5px 5px aliceblue) invert(100%);
   padding-top: 10px;
}

#year{
   font-weight: bold;
   font-style: italic;
   margin-left: 5px;
}